* {
    font-family: Helvetica, Arial, sans-serif;
}

body {
    padding: 60px;
}

/*el color es para el texto dentro de la caja de entrada
la fuente para el texto y largo width para tamaï¿½o de la caja de texto
*/
input {
    color: grey;
    font-size: 1.2rem;
    width: 100%;
}

.card {
    align-content: center;
    border: 1px solid grey;
    border-radius: 20px;
    margin-bottom: 10px;
    margin-right: 10px;
    overflow: hidden;
}

.card p {
    padding: 5px;
    text-align: center;    
}

.card img {
    max-height: 170px;
}

.card-grid {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}
